@import '../../../../../styles/templarios.abstracts';

.product-list {
  &__top {
    margin-bottom: 48px;
  }

  &__filter {
    max-width: 320px;
  }

  &__middle {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(272px, 1fr));
    gap: 20px 32px;
    margin-bottom: 48px;
  }
}
